.project-container {
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2); 
    border-radius: 15px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    padding: 20px;
    background: rgba(255, 255, 255, 0.2)
  }
  
  .project-container:before {
    position: absolute;
    background: inherit;
    z-index: -1;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, .5);
    filter: blur(10px);
    margin: -20px;
  }

.projects{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.projects > * {
    margin-top:5rem
}

.description {
    padding-left:10px
}

.regular {
    display: flex;
    flex-direction: row;
    min-width: 50vw;
    max-width: 50vw
}
.reverse {
    display: flex;
    flex-direction: row-reverse;
    min-width: 50vw;
    max-width: 50vw
}

.description1 {
    padding-right: 10px
}

.project-container > img {
    max-height: 15rem
}

.description, .description1{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


a{
    color: white
}

a:hover{
    color: #F2DD74
}

@media screen and (max-width: 1405px) {
.regular, .reverse{
    display:flex;
    flex-direction: column;
    align-items: center;
    min-width: 60vw;
    max-width: 60vw;
}
img{
    max-width: 15rem;
}

}

@media screen and (max-width: 658px) {
 .regular, .reverse{
     min-width: 85vw;
     max-width: 85vw;
     font-size: 0.8rem;
 }
}