.home-container {
    width: 40vw;
    height: 30vh;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2); 
    border-radius: 20px;
    background-color: rgba(255, 255, 255, .15);
    backdrop-filter: blur(15px);
  }

.intro{
  font-weight: bolder;
  text-align: center;
  padding-top: 1rem;
}

.intro-text{
  padding-left: 1rem;
  padding-right: 1rem;
}

@media screen and (max-width: 1553px) {
  .home-container{
    height: 35vh;
  }
 }

 @media screen and (max-width: 1148px) {
  .home-container{
    width: 55vw;
    height: 35vh;
  }
 }

 @media screen and (max-width: 918px) {
  .home-container{
    width: 65vw;
    height: 35vh;
  }
 }

 @media screen and (max-width: 692px) {
  .home-container{
    width: 70vw;
    height: 50vh;
  }
 }

 @media screen and (max-width: 496px) {
  .home-container{
    width: 80vw;
    height: 55vh;
  }
 }

 @media screen and (max-width: 420px) {
  .home-container{
    width: 90vw;
    height: 60vh;
  }
  .intro{
    font-size:1.5rem;
  }
  .intro-text{
    font-size: 1.2rem;
  }
 }

 @media screen and (max-width: 345px) {
  .home-container{
    width: 80vw;
  }
  .intro{
    font-size:1.2rem;
  }
  .intro-text{
    font-size: 1.0rem;
  }
 }

 @media screen and (max-width: 314px) {
  .home-container{
    width: 90vw;
    height: 80vh;
  }
 }

 @media screen and (max-width: 280px) {
  .home-container{
    width: 80vw;
  }
 }